.nice-select {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #fff;
  color: #000000;
  border-bottom: 2px solid #dbdcdd;
  box-sizing: border-box;
  clear: both;
  cursor: pointer;
  display: block;
  float: left;
  font-family: inherit;
  font-size: 14px;
  font-weight: normal;
  height: 26px;
  line-height: 21px;
  outline: none;
  padding-right: 57px;
  position: relative;
  text-align: left !important;
  transition: all 0.2s ease-in-out;
  user-select: none;
  white-space: nowrap;
  width: 100%; }
  .nice-select:hover {
    border-color: #dbdbdb; }
  .nice-select:active, .nice-select.open {
    border-color: #999; }
  .nice-select:after {
    border-left: 4.5px solid transparent;
    border-right: 4.5px solid transparent;
    border-bottom: 5px solid #ff0000;
    content: '';
    display: block;
    height: 0;
    margin-top: -4px;
    pointer-events: none;
    position: absolute;
    right: 2px;
    top: 50%;
    transform: rotate(180deg);
    transition: all 0.15s ease-in-out;
    width: 0; }
  .nice-select.open:after {
    transform: rotate(0deg);
    border-bottom: 5px solid #6d0d0d; }
  .nice-select.open .list {
    opacity: 1;
    pointer-events: auto;
    transform: scale(1) translateY(0); }
  .nice-select.disabled {
    border-color: #ededed;
    color: #999;
    pointer-events: none; }
    .nice-select.disabled:after {
      border-color: #cccccc; }
  .nice-select.wide {
    width: 100%; }
    .nice-select.wide .list {
      left: 0 !important;
      right: 0 !important; }
  .nice-select.right {
    float: right; }
    .nice-select.right .list {
      left: auto;
      right: 0; }
  .nice-select.small {
    font-size: 12px;
    height: 36px;
    line-height: 34px; }
    .nice-select.small:after {
      height: 4px;
      width: 4px; }
    .nice-select.small .option {
      line-height: 34px;
      min-height: 34px; }
  .nice-select .list {
    font-family: 'Proxima Nova Light Italic', sans-serif;
    background-color: #fff;
    border: 1px solid #c1c1be;
    box-sizing: border-box;
    color: #c2c2c2;
    font-size: 14px;
    margin-top: 2px;
    opacity: 0;
    overflow: hidden;
    padding: 8px 0;
    pointer-events: none;
    position: absolute;
    top: 100%;
    left: 0;
    transform-origin: 50% 0;
    transform: scale(0.75) translateY(-21px);
    transition: all 0.2s cubic-bezier(0.5, 0, 0, 1.25), opacity 0.15s ease-out;
    width: 100%;
    z-index: 9; }
    .nice-select .list:hover .option:not(:hover) {
      background-color: transparent !important; }
  .nice-select .option {
    cursor: pointer;
    font-weight: 400;
    line-height: 40px;
    list-style: none;
    min-height: 40px;
    outline: none;
    padding-left: 18px;
    padding-right: 29px;
    text-align: left;
    transition: all 0.2s; }
    .nice-select .option:hover, .nice-select .option.focus, .nice-select .option.selected.focus {
      color: #6a6a6a; }
    .nice-select .option.selected {
      font-weight: bold; }
    .nice-select .option.disabled {
      background-color: transparent;
      color: #999;
      cursor: default; }

.no-csspointerevents .nice-select .list {
  display: none; }

.no-csspointerevents .nice-select.open .list {
  display: block; }

.project {
  text-align: center;
  padding: 51px 0 15px; }
  .project__table {
    margin: 40px 0; }
    .project__table-line {
      background-color: #ffffff;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 45px;
      padding: 0 14px;
      margin-bottom: 5px;
      cursor: pointer; }
      .project__table-line:last-child {
        margin-bottom: 0; }
  .project .sorted {
    color: #6d6e71; }
  .project .sort__active {
    box-shadow: 0px 6px 16px 0px rgba(51, 51, 51, 0.07);
    color: #000000;
    font-family: 'Proxima Nova Bold', sans-serif; }
  .project .sort-first {
    font-family: 'Proxima Nova Bold', sans-serif; }
  .project .sort__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 74%; }
  .project .sort__row {
    flex-basis: 33.33%; }
  .project .sort__option {
    display: flex;
    align-items: center; }
    .project .sort__option-sm {
      max-width: 89px; }
    .project .sort__option-md {
      max-width: 184px; }
    .project .sort__option-wrap {
      display: flex;
      align-items: center; }
    .project .sort__option-title {
      text-transform: uppercase;
      font-size: 14px;
      margin-right: 7px; }
      .project .sort__option-title__capitalize {
        text-transform: capitalize; }
  .project .sort label {
    margin-right: 32px; }
    .project .sort label .css-checkbox {
      display: none; }
      .project .sort label .css-checkbox:checked + i {
        background: #d11515;
        border-color: #d11515; }
        .project .sort label .css-checkbox:checked + i:hover {
          border-color: #c1c1be; }
          .project .sort label .css-checkbox:checked + i:hover:after {
            opacity: 1; }
        .project .sort label .css-checkbox:checked + i:after {
          opacity: 1;
          border-color: #FFF; }
      .project .sort label .css-checkbox + i {
        box-sizing: border-box; }
        .project .sort label .css-checkbox + i:before, .project .sort label .css-checkbox + i:after {
          box-sizing: border-box; }
      .project .sort label .css-checkbox + i {
        position: relative;
        display: inline-block;
        width: 20px;
        height: 20px;
        margin: 0;
        vertical-align: middle;
        border: 1px solid #bbbdbf;
        border-radius: 2px;
        transition: background 200ms ease;
        background: #ffffff;
        font-size: 20px;
        cursor: pointer; }
        .project .sort label .css-checkbox + i:hover {
          background-color: #c1c1be; }
          .project .sort label .css-checkbox + i:hover:after {
            opacity: 0; }
        .project .sort label .css-checkbox + i:after {
          content: '';
          opacity: 0;
          transform: translateZ(1px) rotate(-45deg);
          outline: 1px solid transparent;
          position: absolute;
          top: 22%;
          left: 15%;
          width: .68em;
          height: .4em;
          border: .15em solid #222;
          border-top: none;
          border-right: none;
          background: rgba(0, 0, 0, 0); }

.select {
